import React from "react";
import "./inversiones.scss";

/* Img */
 import gana from "../../../../assets/Gana.svg";
 import ahorra from "../../../../assets/Ahorra.svg";
 import inverti from "../../../../assets/Inverti.svg";
 import impuestos from "../../../../assets/Impuestos.svg";

 /* Components */
 import Button from "../../../../shared/button/button";
 import CardInversiones from "./components/CardInversiones/cardInversiones";

const Inversiones = () => {
    return(
        <div className="inversiones">
            <h1 className="inversiones_title">Sabas que con el seguro de retiro pods...</h1>
            <div className="inversiones_div">

                <div className="inversiones_cards">
                    <div className="inversiones_cards_dos">
                        <CardInversiones img={gana} title={"Gan siempre"}
                        description={"Es un instrumento de ahorro garantizado. Nunca perders dinero. Tus ahorros crecern por el rendimiento de las inversiones."}/>
                        <CardInversiones img={ahorra} title={"Ahorr tranquilo"}
                        description={"Tus ahorros siempre estarn vigilados por la Superintendencia de Seguros de la Nacin. Transparencia total. Cero Fraude."}/>
                    </div>
                    <div className="inversiones_cards_dos">
                        <CardInversiones img={inverti} title={"Invert lo que puedas"}
                        description={"Vos decids de cunto ser tu ahorro mensual de acuerdo a tus posibilidades. Mxima flexibilidad. Inversin mnima por mes $ 2.000 o U$S 50."}/>
                         <CardInversiones img={impuestos} title={"Menos impuestos"}
                        description={"Tus inversiones mensuales se deducen de la base imponible de impuesto a las ganancias y no estn alcanzados por bienes personales."}/>
                    </div>
                </div>

                <div>
                    <Button title="Ped cotizacin" style={{ width: "427px", height: "103px"}} path="contacto"/>
                </div>
                
            </div>
        </div>
    );
}

export default Inversiones;