import React from "react";
import Pregunta from "./pregunta/Pregunta";
import "./preguntas.scss";

const Preguntas = () => {
    return(
        <div className="preguntas">
            <h1 className="preguntas_title">Preguntas Frecuentes</h1>
            <div className="preguntas_div">
                <Pregunta numero="1" pregunta="Qu estoy contratando?" contenido="Un seguro de retiro de San Cristbal Retiro."/>
                <Pregunta numero="2" pregunta="Qu es un seguro de retiro?" contenido="Es un seguro mediante el cual te comprometes a aportar por un perodo mayor a 10 aos y hasta los 90 aos de edad, una suma de dinero determinada para recibir luego de ese periodo estipulado a la edad jubilatoria, el dinero aportado ms todos los intereses ganados."/>
                <Pregunta numero="3" pregunta="Puedo perder mi dinero ahorrado?" contenido="De ninguna manera, la compaa no solo garantiza una rentabilidad sino que adems tiene una rentabilidad superior a cualquier periodo inflacionario lo que se traduce en un fondo de dinero considerable."/>
                <Pregunta numero="4" pregunta="Cul es la edad para contratar?" contenido="Puede contratar desde los 18 hasta los 74 aos. Cunto antes comiences, mayor posibilidad de acumular dinero."/>
                <Pregunta numero="5" pregunta="Por qu pagara menos impuestos?" contenido="Porque est exento del Impuesto a los Bienes Personales y es deducible de la base imponible del Impuesto a las Ganancias."/>
                <Pregunta numero="6" pregunta="Qu sucede si trascurrido el ao en algn perodo no puedo aportar?" contenido="Tu fondo acumulado se sigue capitalizando."/>
                <Pregunta numero="7" pregunta="Qu son las quitas?" contenido="Es el cargo que se deducir cuando decidas rescindir la pliza antes de los 10 aos. Salvo las excepciones que arriba se detallan."/>
                <Pregunta numero="8" pregunta="Cundo puedo rescatar mi dinero acumulado?" contenido="Cuando tu quieras."/>
                <Pregunta numero="9" pregunta="Puedo hacer un rescate parcial?" contenido="Puedes rescatar parte del fondo acumulado cuando tu quieras."/>
                <Pregunta numero="10" pregunta="Qu sucede si me retiro despus de los 10 aos?" contenido="Retiras los fondos acumulados sin quita alguna."/>
                <Pregunta numero="11" pregunta="Qu pasara si muero antes de retirar el dinero acumulado?" contenido="Se le pagarn los fondos acumulados a los beneficiarios que designes en la solicitud de aseguramiento. Los beneficiarios pueden ser modificados en cualquier momento e ilimitadas veces."/>
                <Pregunta numero="12" pregunta="Cmo recibo la pliza y veo mi saldo acumulado?" contenido="Dentro de los 7 dias de cargada tu solicitud recibirs tu pliza por correo electrnico junto con las instrucciones para consultar tus fondos en momento que desees via web"/>
            </div>
        </div>
    );
}

export default Preguntas;